const Header = props => {
  return (
    <div>
      <AppBar id="appBar">
        <Toolbar>
          <Grid
              container
              justify="space-between"
              alignItems="center"
              spacing={16}
          >
            <Grid item>
              <Chip
                  id="logo"
                  variant="outlined"
                  avatar={<Avatar id="logoIcon">
                    <MaterialUi />
                  </Avatar>}
                  label={
                    <Link to="/">
                      {props.data.site.siteMetadata.title.toUpperCase()}
                    </Link>
                  }
              />
            </Grid>
            <Grid item>
              <Hidden smDown>
                <Typography
                    component="span"
                    variant="caption"
                >
                  <Menu />
                </Typography>
              </Hidden>
              <Hidden mdUp>
                <MenuMobile />
              </Hidden>
            </Grid>
          </Grid>
          <Grid item />
        </Toolbar>
      </AppBar>
      {/*<nav className={classes.drawer} aria-label="mailbox folders">*/}
      {/*  /!* The implementation can be swapped with js to avoid SEO duplication of links. *!/*/}
      {/*  <Hidden smUp implementation="css">*/}
      {/*    <Drawer*/}
      {/*        container*/}
      {/*        variant="temporary"*/}
      {/*        anchor={theme.direction === 'rtl' ? 'right' : 'left'}*/}
      {/*        open={mobileOpen}*/}
      {/*        onClose={handleDrawerToggle}*/}
      {/*        classes={{*/}
      {/*          paper: classes.drawerPaper*/}
      {/*        }}*/}
      {/*        ModalProps={{*/}
      {/*          keepMounted: true // Better open performance on mobile.*/}
      {/*        }}*/}
      {/*    >*/}
      {/*      {drawer}*/}
      {/*    </Drawer>*/}
      {/*  </Hidden>*/}
      {/*  <Hidden xsDown implementation="css">*/}
      {/*    <Drawer*/}
      {/*        classes={{*/}
      {/*          paper: classes.drawerPaper*/}
      {/*        }}*/}
      {/*        variant="permanent"*/}
      {/*        open*/}
      {/*    >*/}
      {/*      {drawer}*/}
      {/*    </Drawer>*/}
      {/*  </Hidden>*/}
      {/*</nav>*/}
    </div>
  );
};

export default props => (
  <StaticQuery
    query={graphql`
      query {
        site {
          siteMetadata {
            title
            contact {
              email
              phone
            }
          }
        }
      }
    `}
    render={data => <Header data={data} />}
  />
);
